/* Portfolio.CSS 07.20 July 2019 by Jeras */
body,
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Montserrat", sans-serif !important;
}
a {
  text-decoration: none;
}
.w3-row-padding img {
  margin-bottom: 12px !important;
}
.w3-sidebar {
  width: 120px !important;
  background: #222 !important;
}
#main {
  margin-left: 120px !important;
}
@media only screen and (max-width: 600px) {
  #main {
    margin-left: 0 !important;
  }
}
#myNavbar .w3-button {
  width: 25%;
}
.w3-sidebar img {
  width: 100%;
}
#about hr,
#portfolio hr,
#contact hr {
  width: 200px;
}
#about img {
  width: 80px;
}
#pricing {
  margin: 0 -16px;
}
#portfolio .w3-row-padding {
  margin: 0 -16px;
}
#portfolio img {
  width: 100%;
}
#about.w3-padding-64 {
  padding-bottom: 0px !important;
}
#about .skills1 {
  height: 28px;
  width: 95%;
}
#about .skills2 {
  height: 28px;
  width: 35%;
}
#about .skills3 {
  height: 28px;
  width: 50%;
}
#about .skills4 {
  height: 28px;
  width: 64%;
}
#about .skills5 {
  height: 28px;
  width: 62%;
}
#about .skills6 {
  height: 28px;
  width: 25%;
}
.active {
  background-color: #ccc;
  background-color: #000;
}
.isDisabled {
  cursor: not-allowed;
}
#main .w3-image {
  border: 2px solid white;
  border-radius: 50%;
  width: 500px;
  opacity: 0.7;
}
